import logo from './logo.svg';
import { Navbar,Container,Nav,NavDropdown,Button } from 'react-bootstrap';
import './App.css';
import { useState } from 'react';
import shoesData from './data';
import ShoesList from './ShoesList';
//import {shoesData, imageUrl} from './data';
import {Link, Route, HashRouter, Switch} from 'react-router-dom';
import Detail from './Detail';
import axios from 'axios';

// import shoesArray from './data.js';
// import Data from './data.js';

function App() {

  let [shoes, setShoes] = useState(shoesData);

  let [detailModalFlag, setDetailModalFlag] = useState(false);

  //
  let [shoesCount, setShoesCount] = useState([10,11,12]);
  

  let [imageUrl, setImageUrl] = useState([
    'https://codingapple1.github.io/shop/shoes1.jpg'
   ,'https://codingapple1.github.io/shop/shoes2.jpg'
   ,'https://codingapple1.github.io/shop/shoes3.jpg']);
  
  return (
    <div className="App">
     <Navbar bg="light" expand="lg">
      <Container>
        <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
            <Nav.Link as={Link} to='/'>Home</Nav.Link>
            <Nav.Link as={Link} to='/detail'>Detail</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>

    <HashRouter basename=''>
      <Switch>
        {/*  */}
          <Route exact path='/'>            
            <MainPage shoes={shoes} setShoes={setShoes} imageUrl={imageUrl}/>
          </Route>

          {/*   */}
          <Route exact path='/detail/:id'>
            <Detail shoes={shoes} imageUrl={imageUrl} detailModalFlag={detailModalFlag} setDetailModalFlag={setDetailModalFlag} shoesCount = {shoesCount} setShoesCount={setShoesCount}/>
          </Route>    

          <Route exact path='/:id'>
            <div>   </div>
          </Route>
      </Switch>
    </HashRouter>
  </div>
  );
}


//(App >>> MainPage)
function MainPage(props){  
  let [loadingModalFlag,setLoadingModalFlag] = useState(false);

  return(

    <div>
      <div className='background'>
        <div>      
          <h1>20% sales event</h1>
          <p>     , 20%  .</p>
          <Button variant="primary"> </Button>{' '}
        </div>
      </div>
            
      <div className='container'>
        <div className='row'>
        {/*    */}
        {
          props.shoes.map((data,i) => {
          //console.log(props.shoes.length);
            return(                                           
                <ShoesList key={i} shoes={props.shoes} i={i} imageUrl={props.imageUrl}/>
            )
          })
        } 
        </div>
      </div>
      
      {
        loadingModalFlag === true ? null : <LoadingAlert />
      }

      <button className='btn btn-primary' onClick={()=>{
        //axios.post('URL',{id : 'dongwonsun', pw : '1234'}).then() axios post ... header / cookie   ...  ...

        axios.get('https://codingapple1.github.io/shop/data2.json')
        .then((result)=>{                    
          setLoadingModalFlag(true);         
          props.setShoes([...result.data, ...props.shoes])
          console.log(`${JSON.stringify(props.shoes)}`);
        })
        .catch(()=>{
          // 
          console.log('');
        })
      }}></button>

    </div>
  )
}

function LoadingAlert(){
    return(
        <div className='loading-alert'>
            <p>.</p>
        </div>
    )
}

export default App;
