import React, {useEffect, useState} from 'react';
import {useHistory, useParams} from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let DivStyled = styled.div`
    padding : 20px;
    font-size : 45px;
`;

let H2Styled = styled.div`
font-size : 30px;
color : ${props => props.color}
`;

// class Detail2 extends React.Component{
//     componentDidMount(){
//         //detail2  Mount   
//     }
//     componentWillUnmount(){
//         //detail2  UnMount   
//     }
// }


function Detail(props){      

    let [alert, setAlert] = useState(true);    
    let [inputData, setInputData] = useState('');

    useEffect(()=>{
        let timer = setTimeout(() => {            
            props.setDetailModalFlag(true);
        }, 2000);        
         return (()=>{
             clearTimeout(timer);
         });
    },[alert]);
    


    let {id} = useParams(); //  uri > ... 0 1 2 ...  
    let history = useHistory();

    //  ...  product() es6   .
    let  = props.shoes.find((x)=>{
        return x.id == id;
    });

    //arrow function    
    // 
    let product = () => {
        if(props.shoes[id].id === parseInt(id)){                        
            return props.shoes[id];
        }
    }

    //  > return 
    let shoesData = product();
    
    return(
        <div className="container">                        
        <DivStyled>
            <H2Styled className='red'>Detail</H2Styled>
        </DivStyled>

        <input onChange={(e)=>{
            setInputData(e.target.value);            
        }}/>
        {inputData}
        {props.detailModalFlag === true ? null : <CountAlert />}
        
        
        {/*
            alert === true ? (<div className='my-alert3'>            
                                <p>   .</p>
                                </div>)
                           : null
        */}

        <div className="row">
            <div className="col-md-6">
            {/*  image      ... */}
            <img src={props.imageUrl[id]} width="100%" alt=''/>
            </div>
            <div className="col-md-6 mt-4">
            <h4 className="pt-5">{shoesData.title}</h4>
            <p>{shoesData.content}</p>
            <p>{shoesData.price}</p>

            {/*  */}
            <Info shoesCount = {props.shoesCount}/>

            <button className="btn btn-danger" onClick={()=>{
                let copyShoesCount = [...props.shoesCount];
                copyShoesCount[0] = copyShoesCount[0] - 1;
                props.setShoesCount(copyShoesCount);
            }}></button>&nbsp;
            <button className="btn  btn-danger" onClick={()=>{
                history.goBack();
            }}></button> 
            </div>
        </div>
    </div>      
    )
}

//
function Info(props){
    return(
        <p> : {props.shoesCount[0]}</p>
    )
}


//   modal alert 
function CountAlert(){
    return(
        <div className='my-alert3'>            
            <p>   .</p>
        </div>
    )
};



export default Detail;
  